<template>
  <div class="students">
    <div class="search-form">
      <Form ref="search" :model="searchData" inline :label-width="40">
        <FormItem label="姓名" prop="name">
          <!-- eslint-disable -->
          <Input size="large" v-model="searchData.name" placeholder="输入学生姓名"></Input>
        </FormItem>
        <FormItem label="科目" prop="subject">
          <Select size="large" style="width:160px" v-model="searchData.subject" placeholder="科目">
            <Option v-for="(label,value) in subjectMaps" :key="value" :value="value">{{label}}</Option>
          </Select>
        </FormItem>
        <FormItem label="是否结课" prop="isDelect" :label-width="80">
          <Select size="large" style="width:160px" v-model="searchData.isDelect" placeholder="是否结课">
            <Option :value="1">已结课</Option>
            <Option :value="0">未结课</Option>
          </Select>
        </FormItem>
        <FormItem :label-width="20">
            <Button size="large" type="primary" @click="search()">查询</Button>
            <Button size="large" @click="reset()" style="margin-left: 8px">重置</Button>
        </FormItem>
      </Form>
    </div>
    <div class="add-part">
      <Button size="large" type="primary" @click="add()">添加</Button>
    </div>
    <div class="table-part">
      <Table :columns="columns" :data="dealData"></Table>
    </div>
    <Modal v-model="modal" :title="editflag?'编辑':'添加'" @on-ok="modalOk" @on-cancel="modalCancel">
      <div class="modal-content">
        <Form ref="addData" :model="addData" :rules="ruleAdd" :label-width="60">
          <FormItem label="姓名" prop="name">
            <Input size="large" :maxlength="4" v-model="addData.name" placeholder="姓名"></Input>
          </FormItem>
          <FormItem label="科目" prop="subject">
            <Select size="large" v-model="addData.subject" placeholder="科目">
              <Option v-for="(label,value) in subjectMaps" :key="value" :value="value">{{label}}</Option>
            </Select>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <Button size="large" @click="modalCancel()" style="margin-right: 10px">取消</Button>
        <Button size="large" type="primary" @click="modalOk()">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { subjects, classes, dates } from '../../mixins/index'
export default {
  mixins: [subjects, classes, dates],
  data () {
    return {
      searchData: {
        name: '',
        subject: '',
        isDelect: ''
      },
      cacheSearch: {
        name: '',
        subject: '',
        isDelect: ''
      },
      data: [],
      columns: [{
        title: '序号',
        type: 'index',
        width: 100,
        align: 'center'
      }, {
        title: '姓名',
        key: 'name',
        align: 'center'
      }, {
        title: '科目',
        key: 'subject',
        align: 'center',
        render: (h, params) => {
          return h('p', this.subjectMaps[params.row.subject])
        }
      }, {
        title: '创建时间',
        key: 'creatTime',
        align: 'center',
        render: (h, params) => {
          return h('p', this.$lib.myMoment(new Date(params.row.creatTime)).formate())
        }
      }, {
        title: '是否结课',
        key: 'isDelect',
        align: 'center',
        render: (h, params) => {
          return h('p', params.row.isDelect ? '已结课' : '未结课')
        }
      }, {
        title: '操作',
        key: 'action',
        width: 200,
        align: 'center',
        render: (h, params) => {
          return h('div', [
            h('a', {
              style: {
                marginRight: '16px'
              },
              on: {
                click: () => {
                  this.edit(params.row)
                }
              }
            }, '编辑'),
            h('a', {
              style: {
                color: '#ed4014',
                marginRight: '16px'
              },
              on: {
                click: () => {
                  this.delectEnter(params.row)
                }
              }
            }, '删除'),
            h('a', {
              style: {
                color: params.row.isDelect ? '#19be6b' : '#ed4014'
              },
              on: {
                click: () => {
                  this.delect(params.row)
                }
              }
            }, params.row.isDelect ? '复课' : '结课')
          ])
        }
      }],
      modal: false,
      addData: {
        name: '',
        subject: ''
      },
      ruleAdd: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' }
        ],
        subject: [
          { required: true, message: '请选择科目', trigger: 'change' }
        ]
      },
      editflag: false,
      editData: {}
    }
  },
  computed: {
    dealData () {
      let { name, subject, isDelect } = this.cacheSearch
      return this.data.filter(s => {
        return (name ? s.name.indexOf(name) >= 0 : true) && (subject ? s.subject.indexOf(subject) >= 0 : true) && (String(isDelect) === '' ? true : String(s.isDelect) === String(!!isDelect))
      })
    }
  },
  created () {
    this.$indexedDB.initStore('students', this.query)
  },
  methods: {
    query () {
      this.$indexedDB.http({
        method: 'get',
        url: 'students'
      }).then((res) => {
        this.data = res.data
      })
    },
    search () {
      this.cacheSearch = { ...this.searchData }
    },
    reset () {
      this.$refs.search.resetFields()
      this.search()
    },
    modalOk () {
      this.$refs.addData.validate((valid) => {
        if (valid) {
          if (this.editflag) {
            this.editPost()
          } else {
            for (const s of this.data) {
              if (s.name === this.addData.name) {
                this.$Message.info(`已有学生${s.name},不允许重复录入`)
                return
              }
            }
            this.addPost()
          }
        }
      })
    },
    add () {
      this.editflag = false
      this.modal = true
    },
    delectEnter (row) {
      this.$Modal.confirm({
        title: '确认',
        content: `是否确认删除学生${row.name}？`,
        onOk: () => {
          for (const c of this.allClassData) {
            if (Number(c.studentId) === Number(row.id)) {
              this.$Message.info(`${row.name}学生在${c.date}有课程安排无法删除`)
              return
            }
          }
          this.delectPost(row)
        }
      })
    },
    delectPost (row) {
      this.$indexedDB.http({
        method: 'delect',
        url: 'students',
        data: row
      }).then((res) => {
        this.$Message.success(res.message)
        this.query()
      })
    },
    modalCancel () {
      this.$refs.addData.resetFields()
      this.modal = false
    },
    addPost () {
      this.$indexedDB.http({
        method: 'add',
        url: 'students',
        data: this.addData
      }).then((res) => {
        this.$Message.success(res.message)
        this.modalCancel()
        this.query()
      })
    },
    editPost () {
      this.$indexedDB.http({
        method: 'put',
        url: 'students',
        data: { ...this.editData, ...this.addData }
      }).then((res) => {
        this.$Message.success(res.message)
        this.modalCancel()
        this.query()
      })
    },
    edit (row) {
      this.modal = true
      this.editflag = true
      this.editData = { ...row }
      this.addData.name = row.name
      this.addData.subject = row.subject
    },
    delect (row) {
      row.isDelect = !row.isDelect
      this.$indexedDB.http({
        method: 'put',
        url: 'students',
        data: row
      }).then((res) => {
        this.$Message.success(res.message)
        this.query()
      })
    }
  }
}
</script>
